@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  background-color: #272932;
}


/* background-color: #FDF500; */
.i{
display: flex;
height: 100vh;
}

.i-left{
flex: 1 1;
display: flex;
align-items: center;
justify-content: center;
color: black;
}

.i-right{
    flex: 1 1;
    position: relative;
}

.i-left-wrapper{
    padding: 50px;
    height: 60%;
    /* background-color: indigo; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
     font-size: 30px;
     font-weight: 300;
}

.i-name{
font-size:  50px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}
@-webkit-keyframes move {
    25%{
        -webkit-transform:translateY(-50px);
                transform:translateY(-50px);
    }
    50%{
        -webkit-transform:translateY(-100px);
                transform:translateY(-100px);
    }
    75%{
        -webkit-transform:translateY(-150px);
                transform:translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}
@keyframes move {
    25%{
        -webkit-transform:translateY(-50px);
                transform:translateY(-50px);
    }
    50%{
        -webkit-transform:translateY(-100px);
                transform:translateY(-100px);
    }
    75%{
        -webkit-transform:translateY(-150px);
                transform:translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #FDF500;
    /* color: seagreen; */
    /* background-color: indigo; */
    display: flex;
    align-items: center;
}

.i-desc{

}

.i-bg{
    -webkit-clip-path: polygon(37% 0, 100% 0, 100% 20%, 100% 80%, 100% 100%, 29% 100%, 0 50%, 24% 20%);
            clip-path: polygon(37% 0, 100% 0, 100% 20%, 100% 80%, 100% 100%, 29% 100%, 0 50%, 24% 20%);
    background-color: #FDF500;
    /* background-color: seagreen; */
    width: 100%;
    height: 100%;
    position:absolute;
    /* z-index: -1; */
    /* Zeby nie bylo kurwa scrola w doł oraz w bok */
    top: 0;
    right: 0;
}
.i-img{
    /* width: 100%; */
    height: 100%;
    object-fit: cover;
    /* ????? beka mozesz zmienic z idex zmiast tego */
    position:absolute;

}
.i-scroll{
    position: absolute;
    bottom: 20px;
}

.a {
    height: 100vh;
    display: flex;
    align-items: center;
  }
  
  .a-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
  }
  
  .a-right {
    flex: 1 1;
    /* color: antiquewhite; */
    color: rgb(12, 10, 10);
    padding: 50px;
  }
  
  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
  }
  
  .a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: -webkit-transform 1s, -webkit-filter 2s ease-in-out;
    transition: transform 1s, filter 2s ease-in-out;
    transition: transform 1s, filter 2s ease-in-out, -webkit-transform 1s, -webkit-filter 2s ease-in-out;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .a-img:hover {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  .a-award-img {
    width: 120px;
    height: 120px;
    border-radius: 20px;
  }
  
  .a-title {
    font-weight: 400;
  }
  
  .a-sub {
    margin: 20px 0px;
  }
  
  .a-desc{
      font-weight: 300;
  }
  
  .a-award{
      margin-top: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  
  .a-award-texts{
      width: 70%;
  }
  
  .a-award-title{
      font-weight: bold;
      color: #555;
      margin-bottom: 10px;
  }
.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    /* color: azure; */
    color: black;
}

.pl-texts{
    width: 65%;
}

.pl-title{
    font-size: 50px;
    font-weight: 600;
}

.pl-desc{
    margin: 20px 0px;   
}

.pl-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid black;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}


.p-browser{
    height: 20px;
    background-color: rgb(8, 8, 8);
    display: flex;
    align-items: center;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
    transition: all 0.5s;
}


.p:hover{
    cursor: pointer;
}

.p-img{
    width: 100%;
    /* height: 100%; */
    transition: all 9s ease;
}

.p:hover  .p-circle{
    background-color: #FDF500;
}

.p:hover .p-img{
    -webkit-transform: translateY(-70%);
            transform: translateY(-70%);
}
.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: seagreen;
    position: absolute;
}

.c-wrapper{
    padding: 50px;
    display: flex;
}

.c-left{
    flex: 1 1;
}
.c-right{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title{
    font-size: 60px;
    width: 80%;
}

.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 0%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.c-desc{
    font-weight: 200;
}

form{
    margin-top: 20px;

}

input{
    width: 60%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button{
    border: none;
    padding: 15px;
    font-size: 16px;
    font-weight: 600;
    background-color: #FDF500;
    color:black;
    cursor: pointer;
}

